.contacts{
    background-color:  rgba(0,0,0, 0.9);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: 0 auto;
    
}
h1{
    font-size: 60px;
    margin: 30px;
    font-weight: 800;
}
p{
    font-size: 20px;
    margin: 20px;
}
 .name,.email{
    width: 400px;
    display: flex;
    flex-direction: column;
    height: 30px;
    padding: 20px;
    font-size: 20px;
    border: 2px white;
    margin: 7px;
    outline: auto;
    color: aliceblue;
    border-radius: 10px;
}
.msg
{
    height: 100px;
    color: aliceblue;
    width: 400px;
    display: flex;
    border-radius: 10px;
    flex-direction: column;
    
    padding: 20px;
    font-size: 20px;
    border: 2px white;
    margin: 7px;
    outline: auto;
}
.submit{
    width: 200px;
    height: 50px;
    align-items: center;
    background-color: aliceblue;

    border-radius: 5px;
    font-size: 30px;
    font-weight: 800;
    margin: 10px;
}
.contact-form{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
}
.submit:active{
    box-shadow:  0 5px white;
    transform: translateY(4px);
    transition: 0.1s;
    outline: none;
    border: none;


}
.submit:hover{
    box-shadow:0 0 20px 5px  rgb(244, 10, 10);
}
.socialmedia li{
    list-style: none;
    display: flex;
    margin: 30px;
    
}


.socialmedia{
    display: flex;
    justify-content: space-around;
    align-items: center;
   


}
.fb{
    height: 60px;
    width: 60px;
    color: white;
    background-color: blue;
    border-radius: 10px;
}
.li{
    height: 60px;
    width: 60px;
    color: blue;
    background-color:white ;
    border-radius: 10px;
}
.wa{
    height: 60px;
    width: 60px;
    color:white;
    border-radius: 10px;
    background-color:rgb(39, 192, 39);
}
.li:hover ,.fb:hover{
    box-shadow:0 0 40px 5px  blue;

}
.wa:hover{
    box-shadow:0 0 40px 5px  rgb(0, 255, 106);

}
@media screen and (max-width:1000px) {
    h1{
        font-size: 50px;
        margin: 10px;
        
    }
    p{
        font-size: 15px;
        text-align: center;
        margin: 20px;}
    .name,.email,.msg{
        width: 300px;}
}