.Home{
    display: flex;
    max-height: 100vh;
    height: 100vh;
    width:"100%";
    justify-content: space-around;
        align-items: center;
        width: 100%;
       
    margin: 0 auto;
    
    background-color: rgba(0,0,0, 0.9);
    
   
}
.profile-img{
    width: 300px;
    height: 300px;
    border: 1px;
    border-radius: 150%;
    
   
    
   
    box-shadow: 0px 5px 40px 20px #05c3f7;
    box-sizing: border-box;
    animation: animate 5s linear infinite;
    transition: 3s;
    margin: 20px;
    
    

}
.profile-img:hover{
    border: 0 0 0 10px solid rgb(68, 237, 11);
    box-shadow: 0px 5px 40px 20px #14cd1a;
    box-sizing: border-box;
    width: 295px;
    height: 295px;
    border: 1px;
    

}
@keyframes  animate{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-50px);
    }

    100%{
        transform: translateY(0px);
    }
}
.firsth1{
    font-size: 90px;
    text-align: left;
    font-weight: 600;
    color: rgb(240, 237, 237);
    margin-bottom: -20px;
    margin-left: -13px;
    width: "100%";
    

    
}
.secondh1{
    font-size: 80px;
    font-weight: 600;
    color: aqua;
    margin-top: -20px;
    text-align: left;
    margin-left: -13px;
    width: 100%;
}

.hello{
    font-size: 20px;
    font-weight: 800;
    color: gray;
    justify-content:center;
    text-align:left;
    position: absolute;
}
.degree{
    display: flex;
    color: gray;
    font-weight: 600;
    justify-content: right;
    width: "100%";
}
@media screen and (max-width:1000px) {
    

    .Home{
        
        
        height:"100%";
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: "100%";
        margin-right: 0;
        position: relative;
        right: 0;
        
        
    }
    .firsth1,.secondh1{
        font-size: 38px;
        margin-top: 1px;
        

    }
    .hello{
        font-size: 10px;
        margin-bottom: 1px;
        margin-left: -10px;
    }
    .profile-img{
        width: auto;
        position: absolute;
        top:150px;
        margin: 1px;
        
        height: 200px;}
    .degree{
        font-size: 10px;
        margin-top: -1px;
    }
    .profile-img:hover{
       
        width: 220px;
        height: 220px;
    }
    .details{
        margin-bottom: -700px;
        position: absolute;
        top: 400px;
        width: "100%";
    }
        
}