.navbar{
    position: sticky;
    top: 0;
    display: flex;
    background-color: rgb(0, 0, 0);
    margin-bottom: 0px;
    width: '100%';
    justify-content: space-around;
    align-items:center;
    color: white;
    max-height: auto;
    z-index: 10;

    

}
.nav-items:hover{
    color: yellow;
    padding-bottom: 0.3rem;
    border-bottom:2px solid yellow ;
    overflow: hidden;

}
.navbar-list{
    position:absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px;
    
    font-size: 30px;
    font-weight: 600;
    color: aliceblue;
    gap:15px;
    

    display:flex;
    cursor: pointer;
    position: relative;
    margin: 5px;
    overflow: hidden;
}
.logos{
    display:flex;
    width: 70px;
    height: 70px;
    border: 2px;
    border-radius: 50%;
    color: aqua;

    justify-content: center;
    align-items: center;

    
    
}

.contact{
    display: inline;
    position:relative;
    top: 50%;
    background-color: white;
    color: black;
    padding: 6px;
    min-width: 120px;
    min-height: 30px;
    border: none;
    border-radius: 22px;
    font-size: 25px;
    font-weight: 900;
    outline: none;
    margin-left: 150px;
    

}
.contact:hover{
    background-color: #20afda;
    transition: 0.7s
}
.contact:active{
    box-shadow:  0 5px white;
    transform: translateY(4px);
    transition: 0.2s;

}
hr{
    
    border:none ;
    width: 90%;
    justify-content: center;
    height: 3px;
    background-color:aqua;
    border-radius: 10px;
    align-items: center;
}
.active{
    color: #2093da;
    background-color: aliceblue;
}
.active{
    color: rgb(0, 200, 255);
    background-color: rgb(241, 241, 251);
    padding: 0.3rem;
    border-radius: 10px;
    background-color: black;
   
    width: auto;
    overflow: hidden;
    font-weight: 900;
}
/*Mob menu*/
.btn-1,.btn-2{
    padding: 5px;
    width: 60px;
    height: 60px;
    color: aqua;
    background-color: transparent;
    border: none;
    outline: none;
}
.menu-btn{
    cursor: pointer;
    border: none;
    outline: none;
    opacity: 0;
    visibility: hidden;
    display: none;
    
    
    
}
@media screen and (max-width:800px) {
    .navbar-list{
        top: 0;
        flex-direction: column;
        position: absolute;
        
        transform: translateY(-100vh);
        display: flex;
        
    
        width: 100%;
        
        
        transition: 1s;
        
        gap: 1.5rem;
        background-color: black;
        left: 0;
        margin-left: -5px;
        border: aqua 1px;
        border-radius: 15px;
    
    }
    .menu-btn{
        visibility: visible;
        display: flex;
        opacity: 1;
    }
    .responsive_nav{
        transform: none;
    }
    .contact{
        margin-left: 0;
    }
    .cut-btn{
        visibility: visible;
        position: absolute;
        top:2px;
        right: 2rem;

    }
}