.about{
    background-color:  rgba(0,0,0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    height: 100vh;
    width: auto;
}
.intro{

    margin: 0;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.header{
    font-size: 60px;
    margin: 30px;
    font-weight: 800 ;

    
}
.intro-psg{
    font-weight: 600;
    color: gray;
    justify-content: center;
    font-size: 18px;
    align-items: center;
    display: flex;
    
}
.work{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    align-items: center;
}
.icon1,.icon2, .icon3{
    width: 80px;
    
    height: 80px;
    background-color: black;
    color: aqua;
    margin: 20px;
    cursor: pointer;
    
}
.icon1:hover,.icon2:hover,.icon3:hover{
    box-shadow: 0 0 40px 5px aquamarine;
}
.icon-info{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-weight: 800;
    transition: 1s;
}
#i1:hover,#i2:hover,#i3:hover{
    font-size: 50px;
    
}
.about-work{
    width: 700px;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
}
@media screen and (max-width:1000px) {
    .about{
        font-size: 10%;
        width: 100%;
    }
    .header{
        font-size: 44px;
        margin: 10px;
        
    
    }  
    #i1:hover,#i2:hover,#i3:hover{
        font-size: 35px;
        
    }
    .intro-psg{
        font-weight: 600;
        align-items: center;
        justify-content: center;
        display: flex;
        flex-direction: column;
       
        font-size: 15px;
        }
        .icon1,.icon2, .icon3{
            width: 60px;
            
            height: 60px;
            
            margin: 20px;
            
        }
} 
